import React, { useContext } from 'react'
import { NavLink } from 'react-router-dom';

import { LoginContext } from './context/LoginContext';

function Header() {

    let {login,setLogin,user,setUser}=useContext(LoginContext);

    function logOut(){

        setLogin(false);
        setUser(null);
        console.log('Logout');

    }

  return (
    <div className='' >
        
        <ul className='nav d-flex justify-content-end bg-red-400 font-semibold'  type='none'  >
        {login==false?(
            <>
            <li className='nav-item m-3 cursor-pointer' >
                <NavLink className='nav-link'   to='' style={{color:"white"}}>Home</NavLink>

            </li>
            <li className='nav-item m-3 cursor-pointer'>
                <NavLink className='nav-link' to='signup'  style={{color:"white"}}>Sign up</NavLink>
                
            </li>

            <li className='nav-item m-3 cursor-pointer'>
                <NavLink className='nav-link' to='signin' style={{color:"white"}}>Sign in</NavLink>
                
            </li>
            </>
       
        ):(
            <><li className='nav-item m-3'>
                <p className='fs-lead' style={{color:"white"}}>Hello, {user}</p></li>
                
                <li className='nav-item m-3'>

                    <NavLink className='nav-link' onClick={logOut} style={{color:"white"}}>Logout</NavLink>

                </li>
                
                </>
        )
        
}
</ul>
    </div>
  )
}

export default Header;