import axios from 'axios';
import React, { useEffect, useState } from 'react';

function Home() {

  const [articles,setArticles]=useState([])

  async function getMovies(){
    const res=await axios.get('http://localhost:5000/user-api/movies');
    if(res.data.message==='Movies List')
    {
      setArticles(res.data.payload);
    }


  }

  useEffect(()=>{
    getMovies()
  },[]);
  return (
    <div>
      <div className='row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4'>
        {articles.map((article)=>(
          <div className='col mt-3' key={article._id}>
            <div className='card  h-100 shadow-sm'>
              <div className='card-body mx-auto'>
                <h5 className='card-title mx-auto'>
                  {article.moviename}

                </h5>
                <img src={article.image} alt='not ok' className='rounded' style={{height:250, width:175}}/>
                <p>Genre: {article.genre}</p>  

                <button className='btn btn-success btn-4 '> Book Tickets</button>              

              </div>

              </div>
            </div>
        ))}
        <div>

        </div>

      </div>
    </div>
  )
}

export default Home;